<template>
  <div class="container">

    <div class="row">
      <div class="col page-head">Stock Cards</div> 
    </div>
    
    <div class="row fn-bar">
      <div class="fn-btn pointer" v-on:click="$router.push({ name: 'Transactions'})">See Transactions</div>
      <div class="fn-btn pointer" v-on:click.prevent="printPreview()">Generate PDF</div>
    </div>
    
    <div class="card stock-info">
       <div class="row">
      
        <div class="col-6">
          <div class="row">
            <div class="col-5">จำนวนชนิดสินค้า</div>
            <div class="col-4 my-auto text-right">{{productsNum}}</div>
            <div class='col my-auto' style='padding-left: 10px;'>ชนิด</div>
          </div>
          <div class="row">
            <div class="col-5">มูลค่าสินค้าคงเหลือรวม</div>
            <div class="col-4 my-auto text-right">{{numberFormat(valueTotal)}}</div>
            <div class='col my-auto' style='padding-left: 10px;'> บาท</div>
          </div>
          <div class="row">
            <div class="col-6">จำนวนสินค้าคงเหลือรวม</div>
            <div class="col-3 my-auto text-right">{{numberFormat(stockTotal)}}</div>
            <div class='col my-auto' style='padding-left: 10px;'>ชิ้น</div>
          </div>
          <div class="row">
            <div class="col-6">จำนวนสินค้ารอจัดส่งรวม</div>
            <div class="col-3 my-auto text-right">{{numberFormat(tbdTotal)}}</div>
            <div class='col my-auto' style='padding-left: 10px;'>ชิ้น</div>
          </div>
        </div>
      </div>
    </div>

    <!-- Search bar -->
    <div class="card">
      <div class='row'>
        <input type="search" class='searchBox col-6' v-model="searchText" placeholder="รหัสสินค้า หรือ ชื่อสินค้า">
      </div>
    </div>
    
    <!-- List table -->
    <div class='card' style='font-size:0.7rem;'>
      <div class='row table-header'>
        <div class="col-2 header-btn" v-on:click="sortItems($event,'productId')">{{att.productId.label}}<span id='productId' class="ml-5 triangle-up"></span></div>
        <div class="col-2 header-btn" v-on:click="sortItems($event,'productName')">{{att.productName.label}}<span id='productName' class="ml-5 triangle-up"></span></div>
        <div class="col-15 header-btn text-right" v-on:click="sortItems($event,'stockqty')">{{att.stockqty.label}}<span id='stockqty' class="ml-5 triangle-up"></span></div>
        <div class="col-15 header-btn text-right" v-on:click="sortItems($event,'valueAcc')">{{att.valueAcc.label}}<span id='valueAcc' class="ml-5 triangle-up"></span></div>
        <div class="col-15 header-btn text-right" v-on:click="sortItems($event,'valueAvg')">{{att.valueAvg.label}}<span id='valueAvg' class="ml-5 triangle-up"></span></div>
        <div class="col-15 header-btn text-right" v-on:click="sortItems($event,'tbdOrder')">{{att.tbdOrder.label}}<span id='tbdOrder' class="ml-5 triangle-up"></span></div>
        <div class="col-15 header-btn text-right" v-on:click="sortItems($event,'stocknet')">{{att.stocknet.label}}<span id='stocknet' class="ml-5 triangle-up"></span></div>

      </div>
      <div v-for="item in filteredList" :key="item.productId">
        <!-- <button class="btn btn-block text-left p-0" > -->
          <div class='row table-item' v-on:click="$router.push({ name: 'CardView', params: { id: item.productId , display:item.productId} })">
            <div class="col-2 my-auto">{{item.productId}}</div>
            <div class="col-2 my-auto">{{item.productName}}</div>
            <div class="col-15 my-auto text-right">{{item.stockqty}}</div>
            <div class="col-15 my-auto text-right">{{numberFormat(item.valueAcc)}}</div>
            <div class="col-15 my-auto text-right">{{numberFormat(item.valueAvg)}}</div>
            <div class="col-15 my-auto text-right">{{item.tbdOrder}}</div>
            <div class="col-15 my-auto text-right">{{item.stocknet}}</div>

          </div>
        <!-- </button> -->
      </div>
    </div>
    <!-- End of List -->

  </div>
</template>

<script>

import * as schema from '../assets/schema'

export default {
  data(){
    return {
      items:this.$store.state.cards,
      att:schema.cardAtt,
      productsNum:0,
      valueTotal:0,
      stockTotal:0,
      tbdTotal:0,
      itemArray:[],

      defaultColumn: 'dateraw',
      sortedColumn: 'dateraw',
      sortedList:[],
      sortASC: {
        productId: true,
        productName: true,
        qty: true,
        stockqty: true,
        valueAcc: true,
        valueAvg: true,
        tbdOrder: true,
        stocknet: true,
      }
    }
  },
  computed:{
    filteredList(){
      let list = this.sortedList.filter((item)=>{
        // let id = item.docId
        let pid = item.productId
        let pnm = item.productName
        let stringArray = [pid,pnm]
        for(let x of stringArray){
          if(x.toUpperCase().match(this.searchText.toUpperCase())){
            return true
          }
        }
        return false
      })
      return list
    },
    searchText: {
      get () {
        return this.$store.state.searchTextCard
      },
      set (value) {
        this.$store.commit('updateSearchText', value)
      }
    }
  },
  methods:{
    numberFormat(num){
      return new Intl.NumberFormat('th-TH',{minimumFractionDigits: 2,maximumFractionDigits:2}).format(num)
    },
    getList(){
      let temp = JSON.parse(JSON.stringify(this.items))
      for(let x in temp){
        this.itemArray.push(temp[x])
      }
    },
    sumInfo(){
      for(let key in this.items){
        this.productsNum++
        this.valueTotal = this.valueTotal + this.items[key].valueAcc
        this.stockTotal = this.stockTotal + this.items[key].stockqty 
        this.tbdTotal = this.tbdTotal + this.items[key].tbdOrder 
      }
    },
    sortItems(e,colName){
      //check if same as current column
      if(colName == this.sortedColumn){
        this.sortASC[colName] = !this.sortASC[colName]
      }

      //remember current column
      this.sortedColumn = colName

      //change arrow
      let el = document.getElementById(colName)
      if(this.sortASC[colName]==true){
        el.classList.remove('triangle-down')
        el.classList.add('triangle-up')
      }else{
        el.classList.remove('triangle-up')
        el.classList.add('triangle-down')
      }

      //add active class
      let hbtn = document.querySelectorAll('.header-btn') 
      for(let x of hbtn){
        x.classList.remove('active')
      }
      e.target.classList.add('active')


      //sort items
      let list = JSON.parse(JSON.stringify(this.itemArray))
      list.sort((a,b)=>{
        switch (colName) {
          case 'dateraw':
            return a.dateraw-b.dateraw
          case 'docId':
            return a.docId.toString().localeCompare(b.docId)
          case 'productId':
            return a.productId.toString().localeCompare(b.productId)
          case 'productName':
            return a.productName.toString().localeCompare(b.productName)
          case 'type':
            return a.type.toString().localeCompare(b.type)
          case 'qty':
            return a.qty-b.qty
          case 'valueAcc':
          case 'valueAvg':
          case 'stockqty':
          case 'stocknet':
          case 'tbdOrder':
            return a[colName]-b[colName]
          default:
            break;
        }
      })

      //if descending order
      if(this.sortASC[colName]==false){
        list.reverse()
      }
      this.sortedList = list
    },
    printPreview(){
      let cards = JSON.parse(JSON.stringify(this.$store.state.cards))
      window.sessionStorage.setItem('cards',JSON.stringify(cards))
      window.open('/hunter-stock-card/print.html', '_blank')
    },
  },
  created(){
    this.sumInfo()
    this.getList()

    //set default sortedList
    this.sortedList = JSON.parse(JSON.stringify(this.itemArray))
  },
}
</script>
