<template>
  <div class="container">
    
    <div class="row">
      <div class="col page-head">Transactions</div> 
    </div>

    <div class="row fn-bar fn-list">
      <!-- <div class="fn-btn pointer" v-on:click.prevent="printPreview()">Generate PDF</div> -->

        <div>[Date Review]: </div>
        <div>จำนวนรายการ: {{info.numberTotal}}</div>
        <div>รายการเข้า: {{info.numberIn}}</div>
        <div>รายการออก: {{info.numberOut}}</div>
        <div>จำนวนรับเข้า: {{info.qtyIn}}</div>
        <div>จำนวนจ่ายออก: {{info.qtyOut}}</div>

    </div>

    <!-- Main function bar  -->
    <div class="row fn-bar">
      <div class="fn-btn pointer" v-on:click="$router.push({ name: 'Cards'})">See Stock Card</div>
    </div>

    <!-- Search bar -->
    <div class="card">
      <div class='row'>
        <input type="search" class='searchBox col-6' v-model="searchText" placeholder="รหัสเอกสาร หรือ รหัสสินค้า หรือ ชื่อสินค้า">
      </div>
    </div>

    <!-- List table -->
    <div class='card' style='font-size:0.7rem;'>
      <div class='row table-header'>
        <div class="col-1">{{att.id.label}}</div>
        <div class="col-1 header-btn" v-on:click="sortItems($event,'dateraw')">{{att.date.label}}<span id='dateraw' class="ml-5 triangle-up"></span></div>
        <div class="col-2 header-btn" v-on:click="sortItems($event,'docId')">{{att.docId.label}}<span id='docId' class="ml-5 triangle-up"></span></div>
        <div class="col-2 header-btn" v-on:click="sortItems($event,'productId')">{{att.productId.label}}<span id='productId' class="ml-5 triangle-up"></span></div>
        <div class="col-2 header-btn" v-on:click="sortItems($event,'productName')">{{att.productName.label}}<span id='productName' class="ml-5 triangle-up"></span></div>
        <div class="col-1 header-btn" v-on:click="sortItems($event,'type')">{{att.type.label}}<span id='type' class="ml-5 triangle-up"></span></div>
        <div class="col-1 header-btn text-right" v-on:click="sortItems($event,'qty')">{{att.qty.label}}<span id='qty' class="ml-5 triangle-up"></span></div>
        <div class="col-2 text-right">{{att.cost.label}}</div>
      </div>
      <div v-for="item in filteredList" :key="item.id">
        <!-- <button class="btn btn-block text-left p-0" v-on:click="$router.push({ name: 'CustomerView', params: { id: id , display:item.docProps.id} })"> -->
          <div class='row table-item'>
            <div class="col-1 my-auto">{{item.id}}</div>
            <div class="col-1 my-auto">{{item.date}}</div>
            <div class="col-2 my-auto">{{item.docId}}</div>
            <div class="col-2 my-auto">{{item.productId}}</div>
            <div class="col-2 my-auto">{{item.productName}}</div>
            <div class="col-1 my-auto">{{item.type}}</div>
            <div class="col-1 my-auto text-right">{{item.qty}}</div>
            <div class="col-2 my-auto text-right">{{numberFormat(item.cost)}}</div>
          </div>
        <!-- </button> -->
      </div>
    </div>
    <!-- End of List -->

  </div>
</template>

<script>

import * as schema from '../assets/schema'

export default {
  data(){
    return {
      // items:this.$route.params.items,
      items:this.$store.state.transactions,
      att:schema.transactionAtt,
      info: this.$store.state.info,
      searchText: '',
      defaultColumn: 'dateraw',
      sortedColumn: 'dateraw',
      sortedList:[],
      sortASC: {
        dateraw: true,
        docId: true,
        productId: true,
        productName: true,
        type: true,
        qty: true,
      }
    }
  },
  computed:{
    filteredList(){
      let list = this.sortedList.filter((item)=>{
        let id = item.docId.toString()
        let pid = item.productId.toString()
        let pnm = item.productName
        let stringArray = [id,pid,pnm]
        for(let x of stringArray){
          if(x.toUpperCase().match(this.searchText.toUpperCase())){
            return true
          }
        }
        return false
      })
      return list
    },
    
  },
  methods:{
    numberFormat(num){
      return new Intl.NumberFormat('th-TH',{minimumFractionDigits: 2,maximumFractionDigits:2}).format(num)
    },
    sortItems(e,colName){
      //check if same as current column
      if(colName == this.sortedColumn){
        this.sortASC[colName] = !this.sortASC[colName]
      }

      //remember current column
      this.sortedColumn = colName

      //change arrow
      let el = document.getElementById(colName)
      if(this.sortASC[colName]==true){
        el.classList.remove('triangle-down')
        el.classList.add('triangle-up')
      }else{
        el.classList.remove('triangle-up')
        el.classList.add('triangle-down')
      }

      //add active class
      let hbtn = document.querySelectorAll('.header-btn') 
      for(let x of hbtn){
        x.classList.remove('active')
      }
      e.target.classList.add('active')


      //sort items
      let list = JSON.parse(JSON.stringify(this.items))
      list.sort((a,b)=>{
        switch (colName) {
          case 'dateraw':
            return a.dateraw-b.dateraw
          case 'docId':
            return a.docId.toString().localeCompare(b.docId)
          case 'productId':
            return a.productId.toString().localeCompare(b.productId)
          case 'productName':
            return a.productName.toString().localeCompare(b.productName)
          case 'type':
            return a.type.toString().localeCompare(b.type)
          case 'qty':
            return a.qty-b.qty
          default:
            break;
        }
      })

      //if descending order
      if(this.sortASC[colName]==false){
        list.reverse()
      }
      this.sortedList = list
    },
  },
  created(){
    //set default sortedList
    this.sortedList = JSON.parse(JSON.stringify(this.items))
    //this.sortItems(this.defaultColumn)
  },
}
</script>
