<template>
  <div class="container">

    <div class="row">
      <div class="col page-head">Upload Excel File</div> 
    </div>

    <!-- Main function bar  -->
    <div class="row fn-bar">
      <div class="fn-btn pointer" v-on:click.prevent="getTemplate()">Download Template</div>
      <div class="fn-btn pointer" v-on:click.prevent="useExample()">ใช้ข้อมูลตัวอย่าง</div>
      <div class="fn-btn pointer" v-on:click.prevent="startOperation()" v-if='jsonData'>ยืนยันข้อมูล</div>
    </div>
    
    <!-- Average cost method switch -->
    <div class='card'>
      <div class="row fn-bar">
        <div class='col-2 my-auto p-0 f-08'>เลือกวิธีคำนวนมูลค่าเฉลี่ย : </div>
        <div id='switch' class="fn-btn active pointer col-2" v-on:click.prevent="methodSwitch($event,'prd')">เฉลี่ยทั้งปี (Periodic)</div>
        <div id='switch' class="fn-btn pointer col-2" v-on:click.prevent="methodSwitch($event,'ppt')">เฉลี่ยทุกครั้งที่เคลื่อนไหว (Perpetual)</div>
      </div>
    </div>

    <div id='fileinput' class="card">
      <!-- File input box -->
      <div>
        <div class="general-head f-08">เลือกไฟล์ของคุณ:</div>
        <input id="input-file" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel">
        <!-- <div class="fn-btn pointer" v-on:click.prevent="saveFile()" v-if='fileName'>บันทึกไฟล์ไว้ใช้คราวหน้า</div> -->
      </div>
      <!-- End file input box -->

      <!-- Data Preview -->
      <div class='my-05' v-if='jsonData'>
        <!-- ***Using table to display preview because data size is unpredictable*** -->
        <div>Data Preview:</div>
        <div class='infobox'>
          <table>
            <thead>
              <tr>
                <th v-for='column of fileHeader' :key='column'>{{column}}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for='row of jsonData' :key='jsonData.indexOf(row)'>
                <td v-for='(cell,key) in row' :key='key'>{{cell}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- End Data Preview -->
    </div>

  </div>
</template>

<script>

import {db} from '../firebaseinit'
import XLSX from 'xlsx'
import * as example from '../assets/example'
import * as schema from '../assets/schema'

export default {

  data(){
    return {
      // databaseName:'',
      // databaseList:['Customer','Product'],
      // dataType:['text','date','number'],
      
      fileHeader:[],
      fileName:'',
      fileElement: [],
      jsonData:'',
      isDisabled: true,
      showList: false,
      info: {
        numberTotal:0,
        numberIn:0,
        numberOut:0,
        qtyIn:0,
        qtyOut:0,
      },
      avgMethod:'prd'
    } 
  },
  methods:{
    excelToJsDate(eDate){
      return new Date((eDate-25569)*86400000)
    },
    dateFormat(dateObj){
      let dd = ('0'+ dateObj.getDate()).slice(-2)
      let mm = ('0' + (dateObj.getMonth()+1)).slice(-2)
      let yy = dateObj.getFullYear()
      return `${dd}/${mm}/${yy}`
    },
    numberFormat(num){
      return new Intl.NumberFormat('th-TH',{minimumFractionDigits: 2}).format(num)
    },
    inputElement(){
      this.fileElement = document.getElementById('input-file')
      this.fileElement.addEventListener('change',this.handleFile)
    },
    handleFile(e){
      if(e.target.files.length>0){
        this.isDisabled = false
        console.log(`now isDisabled = ${this.isDisabled}`)
        const file = e.target.files[0]
        console.log(`you've selected a file = ${file.name}`)
        this.fileName = file.name
        const reader = new FileReader()
        reader.onload = (e)=>{
          let data = e.target.result
          let workbook = XLSX.read(data, {type:'binary'})
          let forHeader = XLSX.utils.sheet_to_json(workbook.Sheets[workbook.SheetNames[0]],{header:1},{defval:''})
          this.fileHeader = forHeader[0]
          let result = XLSX.utils.sheet_to_json(workbook.Sheets[workbook.SheetNames[0]],{header:1},{defval:''})
          this.jsonData = result
          console.log(JSON.stringify(result))
          this.jsonData = this.jsonData.slice(1)
        }
        reader.readAsBinaryString(file)
      }else{
        console.log(`no file is selected`)
        console.log(`now isDisabled = ${this.isDisabled}`)
      }
    },
    saveFile(){
      db.collection('UploadedFiles').add({
        name: this.fileName,
        header: this.fileHeader,
        data: this.jsonData,
      })
    },
    startOperation(){
      //reset data
      this.$store.state.transactions = []
      this.$store.state.products = []
      this.$store.state.cards = {}
      
      //loop 1 - arrange raw data into transactions - sort by date
      this.getTransactions()
      
      //loop 2 - create product list - assign transactions to their respective cards
      this.getInOut()
      
      //loop through cards to create their history
      if(this.avgMethod=='prd'){
        this.acPeriodic()
      }else if(this.avgMethod=='ppt'){
        this.acPerpetual()
      }

      //go to page Transactions
      this.$router.push({name: 'Transactions'})
    },
    getTransactions(){
      let temp = []
      this.jsonData.forEach((row)=>{
        let item ={
          docId: row[1],
          day :row[2],
          month :row[3],
          year :row[4],
          productId :row[5],
          productName :row[6],
          type :row[7],
          qty :row[8],
          dateraw :new Date(row[4],row[3]-1,row[2]),
        }
        item.date = this.dateFormat(item.dateraw)
        if(item.type=='IN'){item["cost"]=row[9]}
        temp.push(item)
      })
      temp.sort((a,b)=>{
        return a.dateraw - b.dateraw || a.type.localeCompare(b.type) || a.docId.toString().localeCompare(b.docId)
      })
      for(let i=0;i<temp.length;i++){
        let id= 'T'+this.genId(i+1)
        temp[i]['id']=id
        // this.$store.state.transactions[id] = temp[i]
      }
      this.$store.state.transactions = temp
    },
    resetData(){
      console.log(`you clicked reset data`)
      this.jsonData = ''
      this.fileHeader = []
      document.getElementById('input-file').value = ""
      this.isDisabled = !this.isDisabled
      console.log(`now jsonData length = ${this.jsonData.length}`)
      console.log(`now isDisabled = ${this.isDisabled}`)
    },
    getInOut(){
      let obj = JSON.parse(JSON.stringify(this.$store.state.transactions))
      let cti = 0
      let cto = 0
      for(let key in obj){
        let item = obj[key]

        //Create new product if meet this product id for the first time
        this.addProduct(item)
        
        if(item.type=='IN'){
          cti++
          item['refId'] = obj[key].id
          item['id'] = 'I'+this.genId(cti)
          item['matched'] = 0
          item['remain'] = item.qty 
          this.info.numberIn++
          this.info.qtyIn=this.info.qtyIn+item.qty
          this.$store.state.cards[item.productId].listIn.push(item)
          this.$store.state.cards[item.productId].listAll.push(item)
        }

        if(item.type=='OUT'){
          cto++
          item['refId'] = obj[key].id
          item['id'] = 'O'+this.genId(cto)
          item['matched'] = 0
          item['remain'] = item.qty 
          this.info.numberOut++
          this.info.qtyOut=this.info.qtyOut+item.qty
          this.$store.state.cards[item.productId].listOut.push(item)
          this.$store.state.cards[item.productId].listAll.push(item)
        }
      }
      this.info.numberTotal = this.info.numberIn + this.info.numberOut
      this.$store.state.info = this.info
    },
    addProduct(item){
      let count=0
      let pid = item.productId
      let pname = item.productName
      for(let n of this.$store.state.products){
        if(n.productId && n.productId == pid){
          count++
        }
      }
      if(count==0){
        this.$store.state.products.push({
          productId: pid,
          productName: pname,
        })
        this.$store.state.cards[pid]= JSON.parse(JSON.stringify(schema.card))
        this.$store.state.cards[pid].productId = pid
        this.$store.state.cards[pid].productName = pname
      }else{
        // console.log(`duplicate!`)
      }
    },
    genId(count){
      let x = count
      let id = ''
      if(x<10){
        id='000'+x
      }else if(x<100){
        id='00'+x
      }else if(x<1000){
        id='0'+x
      }else if(x<10000){
        id=''+x
      }
      return id
    },
    methodSwitch(e,method){
      let sw = document.querySelectorAll('#switch') 
      for(let x of sw){
        x.classList.remove('active')
      }
      e.target.classList.add('active')
      this.avgMethod = method
    },
    acPeriodic(){
      let temp = JSON.parse(JSON.stringify(this.$store.state.cards))
      
      //loop for each product id
      for(let id in temp){
        let stockqty = 0
        let valueAcc = 0
        let valueAvg = 0
        let count = 0
        let listIn = temp[id].listIn
        let list=temp[id].listAll
        let queue=[]
        // let listOut = temp[id].listOut

        //loop for calculating avg value
        for(let key in listIn){
          let itemIn = listIn[key]
          stockqty = stockqty + itemIn.qty
          valueAcc = valueAcc + (itemIn.qty*itemIn.cost)  
        }
        valueAvg = valueAcc/stockqty
        stockqty = 0
        valueAcc = 0

        //loop for arrange history
        for(let key in list){
          
          let item = list[key]
          let rec = this.getRecord(item)
          
          if(item.type=='IN'){
            
            // console.log('IN')

            count++
            rec.id = 'R'+this.genId(count)

            rec.costTotal = (item.qty*item.cost)
            stockqty = stockqty+item.qty
            valueAcc = valueAcc+(item.qty*item.cost)
            // valueAvg = valueAcc/stockqty

            rec.stockqty = stockqty
            rec.valueAcc = valueAcc
            rec.valueAvg = valueAvg
            rec.remark = '-'
            rec.status = '-'
            temp[id].history[rec.id] = JSON.parse(JSON.stringify(rec))
          
          }else if(item.type=='OUT'){

            // console.log('OUT')

            if(stockqty>=item.qty){
              
              // console.log('stock ok')

              count++
              let rec = this.getRecord(item)
              rec.id = 'R'+this.genId(count)

              rec.cost = valueAvg
              rec.costTotal = (item.qty*rec.cost)
              stockqty = stockqty-item.qty
              valueAcc = valueAcc-(item.qty*rec.cost)
              // valueAvg = valueAcc/stockqty

              rec.stockqty = stockqty
              rec.valueAcc = valueAcc
              rec.valueAvg = valueAvg
              rec.remark = '-'
              rec.status = '-'
              temp[id].history[rec.id] = JSON.parse(JSON.stringify(rec))
            }else{
              // console.log('not enough stock')
              queue.push(item)
            }

          }
          // console.log('q length = '+queue.length)

          if(queue.length>0){
            // console.log('q length > 0')

            while(queue.length>0){
              // console.log('q length = '+queue.length)
              
              if(queue[0].qty<=stockqty){
                // console.log('process 2')
                // console.log('loop for = '+queue[0].id)+' (queue)'

                count++
                let item = queue[0]
                let rec = this.getRecord(item)
                rec.id = 'R'+this.genId(count)

                rec.cost = valueAvg
                rec.costTotal = (item.qty*rec.cost)
                stockqty = stockqty-item.qty
                valueAcc = valueAcc-(item.qty*rec.cost)
                // valueAvg = valueAcc/stockqty

                rec.stockqty = stockqty
                rec.valueAcc = valueAcc
                rec.valueAvg = valueAvg
                rec.remark = 'From '+rec.refId+' '+rec.date
                rec.status = '-'
                temp[id].history[rec.id] = JSON.parse(JSON.stringify(rec))

                queue.shift()
              }else{
                // console.log('not enough stock')
                break
              }
            }
          }
          
        }

        if(queue.length>0){
          let ctbd = 0
          for(let i=0;i<queue.length;i++){
            ctbd++
            let item = queue[i]
            let rec = this.getRecord(item)
            rec.id = 'TBD'+this.genId(ctbd)
            rec.remark = 'From '+rec.refId+' '+rec.date
            rec.status = 'ไม่พอจัดส่ง'
            temp[id].historyTBD[rec.id] = JSON.parse(JSON.stringify(rec))
            temp[id].tbdOrder = temp[id].tbdOrder+item.qty
          }
        }

        temp[id].stockqty = stockqty
        temp[id].valueAcc = valueAcc
        temp[id].valueAvg = valueAvg
        temp[id].stocknet = temp[id].stockqty-temp[id].tbdOrder
      }
      this.$store.state.cards = JSON.parse(JSON.stringify(temp))
      
    },
    acPerpetual(){
      let temp = JSON.parse(JSON.stringify(this.$store.state.cards))
      for(let id in temp){
        let stockqty = 0
        let valueAcc = 0
        let valueAvg = 0
        let count = 0
        let list=temp[id].listAll
        let queue=[]
        
        // console.log('loop for = '+temp[id].productId)

        for(let key in list){
          
          // console.log('process 1')
          // console.log('loop for = '+list[key].id)

          let item = list[key]
          let rec = this.getRecord(item)
          
          if(item.type=='IN'){
            
            // console.log('IN')

            count++
            rec.id = 'R'+this.genId(count)

            rec.costTotal = (item.qty*item.cost)
            stockqty = stockqty+item.qty
            valueAcc = valueAcc+(item.qty*item.cost)
            valueAvg = valueAcc/stockqty

            rec.stockqty = stockqty
            rec.valueAcc = valueAcc
            rec.valueAvg = valueAvg
            rec.remark = '-'
            rec.status = '-'
            temp[id].history[rec.id] = JSON.parse(JSON.stringify(rec))
          
          }else if(item.type=='OUT'){

            // console.log('OUT')

            if(stockqty>=item.qty){
              
              // console.log('stock ok')

              count++
              let rec = this.getRecord(item)
              rec.id = 'R'+this.genId(count)

              rec.cost = valueAvg
              rec.costTotal = (item.qty*rec.cost)
              stockqty = stockqty-item.qty
              valueAcc = valueAcc-(item.qty*rec.cost)
              valueAvg = valueAcc/stockqty

              rec.stockqty = stockqty
              rec.valueAcc = valueAcc
              rec.valueAvg = valueAvg
              rec.remark = '-'
              rec.status = '-'
              temp[id].history[rec.id] = JSON.parse(JSON.stringify(rec))
            }else{
              // console.log('not enough stock')
              queue.push(item)
            }

          }
          // console.log('q length = '+queue.length)

          if(queue.length>0){
            // console.log('q length > 0')

            while(queue.length>0){
              // console.log('q length = '+queue.length)
              
              if(queue[0].qty<=stockqty){
                // console.log('process 2')
                // console.log('loop for = '+queue[0].id)+' (queue)'

                count++
                let item = queue[0]
                let rec = this.getRecord(item)
                rec.id = 'R'+this.genId(count)

                rec.cost = valueAvg
                rec.costTotal = (item.qty*rec.cost)
                stockqty = stockqty-item.qty
                valueAcc = valueAcc-(item.qty*rec.cost)
                valueAvg = valueAcc/stockqty

                rec.stockqty = stockqty
                rec.valueAcc = valueAcc
                rec.valueAvg = valueAvg
                rec.remark = 'From '+rec.refId+' '+rec.date
                rec.status = '-'
                temp[id].history[rec.id] = JSON.parse(JSON.stringify(rec))

                queue.shift()
              }else{
                // console.log('not enough stock')
                break
              }
            }
          }
          
        }

        if(queue.length>0){
          let ctbd = 0
          for(let i=0;i<queue.length;i++){
            ctbd++
            let item = queue[i]
            let rec = this.getRecord(item)
            rec.id = 'TBD'+this.genId(ctbd)
            rec.remark = 'From '+rec.refId+' '+rec.date
            rec.status = 'ไม่พอจัดส่ง'
            temp[id].historyTBD[rec.id] = JSON.parse(JSON.stringify(rec))
            temp[id].tbdOrder = temp[id].tbdOrder+item.qty
          }
        }

        temp[id].stockqty = stockqty
        temp[id].valueAcc = valueAcc
        temp[id].valueAvg = valueAvg
        temp[id].stocknet = temp[id].stockqty-temp[id].tbdOrder
      }
      this.$store.state.cards = JSON.parse(JSON.stringify(temp))
    },
    getRecord(item){
      let rec = JSON.parse(JSON.stringify(schema.history))
      for(let x in rec){
        if(item[x]) rec[x]=item[x]
      }
      rec.ioId = item.id
      return rec
    },
    useExample(){
      this.fileHeader = example.header
      this.jsonData = example.data
    },
    getTemplate(){
      const wb = XLSX.utils.book_new()
      const ws_data = []
      let temp = {}
      for(let field of example.header){
        temp[field] = ''
      }
      ws_data.push(temp)
      // console.log(JSON.stringify(ws_data))
      const ws = XLSX.utils.json_to_sheet(ws_data)
      let templateName = 'stock-card-template'
      XLSX.utils.book_append_sheet(wb,ws,templateName)
      XLSX.writeFile(wb, templateName+".xlsx")
    },
  },

  created(){

  },
  mounted(){
    this.inputElement()
  },
}
</script>
