import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state:{
    transactions:[],
    products:[],
    cards:{},
    info:{},
    searchTextCard:'',
  },
  mutations:{
    updateSearchText(state,value){
      state.searchTextCard = value
    }
  }
})
