<template>
  <div class="container">
    <div class="row">
      <div class="bigbutton col-3" v-on:click="$router.push({ name: 'Upload'})">เริ่มโปรเจ็คใหม่</div>
    </div>
    <div class="row">
      <div class="bigbutton col-3" v-on:click.prevent="getTemplate()">Download Template</div>
    </div>
    <div class="row">
      <div class="col text-center">ตัวอย่างตารางข้อมูลดิบ</div> 
    </div>
    
    <!-- List table -->
    <div class='card' style='font-size:0.7rem;'>
      <div class='flex-container table-header'>
        <div class="col-1">ลำดับ</div>
        <div class="col-2">หมายเลขเอกสาร</div>
