        <div class="col-2">ชื่อสินค้า</div>
        <div class="col-15">ประเภท(เข้า/ออก)</div>
        <div class="col-05">จำนวน</div>
        <div class="col-15">ราคาต่อหน่วย</div>
      </div>
      <div v-for="ex in examples" :key="examples.indexOf(ex)">
        <!-- <button class="btn btn-block text-left p-0" v-on:click="$router.push({ name: 'CustomerView', params: { id: id , display:item.docProps.id} })"> -->
          <div class='flex-container table-item'>
            <div class="col-1">{{ex[0]}}</div>
