</template>

<script>

import * as example from '../assets/example'
import XLSX from 'xlsx'

export default {
  data(){
    return {
      examples :example.data
    }
  },
  methods:{
    getTemplate(){
      const wb = XLSX.utils.book_new()
      const ws_data = []
      let temp = {}
      for(let field of example.header){
        temp[field] = ''
      }
      ws_data.push(temp)
      console.log(JSON.stringify(ws_data))
      const ws = XLSX.utils.json_to_sheet(ws_data)
      let templateName = 'stock-card-template'
      XLSX.utils.book_append_sheet(wb,ws,templateName)
      XLSX.writeFile(wb, templateName+".xlsx")
    },
  }
}
</script>
